@mixin flexCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

$font-regular: "font-1", sans-serif;
$font-medium: "font-2", sans-serif;
$font-bold: "font-3", sans-serif;

$color-1: #2c3e50;
$color-2: #00bc77;


* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    color: $color-1;
}

html {
    font-family: Avenir, Helvetica, Arial, sans-serif; // A modifier : Avenir
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: $color-1;
    // background-color: red;
}

body {
    margin: 0;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

li {
    list-style-type: none;
}

p {
    font-size: .9rem;
    font-weight: 300;
    letter-spacing: .05rem;
    margin-top: 1.5rem;
}

h1 {
    line-height: 1.45;
}

.main {
    display: block;
    flex: 1;
}

.sr-only {
    border: 0 !important;
    clip: rect(1px, 1px, 1px, 1px) !important; /* 1 */
    -webkit-clip-path: inset(50%) !important;
    clip-path: inset(50%) !important; /* 2 */
    height: 1px !important;
    margin: -1px !important;
    overflow: hidden !important;
    padding: 0 !important;
    position: absolute !important;
    width: 1px !important;
    white-space: nowrap !important; /* 3 */
}

.spinner {
    font-size: 35rem;
}